.content.groups__add__update .sub__nav,
.content.groups__add__update>.page__title__wrap {
  grid-column: span 1 / span 1;
}

.content.groups__add__update .sub__nav .right button:first-of-type {
  margin-right: 16px;
}

.section__grid__wrap.groups__add__update {
  grid-template-columns: repeat(1, minmax(0, 1fr));
}

.groups__add__update .group__info .section__inner>ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  flex-wrap: wrap;
}

.groups__add__update .group__info .section__inner>ul>li {
  width: 48%;
}

.groups__add__update .group__info .section__inner>ul>li select {
  width: 100%;
}

.groups__add__update .group__info .section__inner>ul>li:first-of-type,
.groups__add__update .group__info .section__inner>ul>li:nth-of-type(2) {
  margin-bottom: 24px;
}

.groups__add__update .group__info .section__inner>dl,
.groups__add__update .group__info .section__inner>ul>li>dl {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.groups__add__update .group__info .section__inner>dl dt,
.groups__add__update .group__info .section__inner>ul>li>dl>dt {
  margin-bottom: 12px;
  white-space: nowrap;
}

.groups__add__update .group__info .section__inner>ul>li>dl>dd {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.groups__add__update .group__info .section__inner>ul>li>dl>dd.group__name {
  display: block;
}

.groups__add__update .group__info .section__inner>dl dd,
.groups__add__update .group__info .section__inner>ul>li>dl>dd input {
  width: 100%;
}

.groups__add__update .group__info .section__inner>ul>li>dl>dd button {
  margin-left: 24px;
}

.groups__add__update .group__info .section__inner>dl dd textarea {
  min-height: 96px;
}

.groups__add__update .ota__info .list__wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.groups__add__update .ota__info .list__wrap>li {
  margin-bottom: 24px;
  width: 49%;
}

.groups__add__update .ota__info .list__wrap>li:nth-of-type(3),
.groups__add__update .ota__info .list__wrap>li:last-of-type {
  width: 100%;
}

.groups__add__update .ota__info .list__wrap>li:last-of-type {
  margin-bottom: 0px;
}

.groups__add__update .ota__info .list__wrap>li>dl {
  display: flex;
  align-items: center;
}

.groups__add__update .ota__info .list__wrap>li>dl .react-datepicker-wrapper {
  min-width: 150px;
}

.groups__add__update .ota__info .list__wrap>li>dl>dt {
  margin-right: 16px;
  white-space: nowrap;
}

.groups__add__update .ota__info .list__wrap>li>dl dd,
.groups__add__update .ota__info .list__wrap>li>dl dd input {
  width: 100%;
}

.groups__add__update .ota__info .list__wrap>li>dl dd textarea {
  min-height: 96px;
}

.groups__add__update .ota__info .list__wrap>li>dl>dd>ul {
  display: flex;
  align-items: center;
}

.groups__add__update .ota__info .list__wrap>li:nth-of-type(2) dd>ul>li {
  margin-right: 16px;
}

.groups__add__update .ota__info .list__wrap>li:nth-of-type(2) li:last-of-type {
  margin-right: 0px;
}

@media (max-width: 1050px) {
  .groups__add__update .ota__info .list__wrap>li {
    width: 100%;
  }
}

@media (max-width: 650px) {
  .groups__add__update .group__info .section__inner>ul {
    flex-direction: column;
  }

  .groups__add__update .group__info .section__inner>ul>li {
    width: 100%;
    margin-bottom: 24px;
  }

  .groups__add__update .group__info .section__inner>ul>li:first-of-type {
    margin-bottom: 16px;
  }

  .groups__add__update .group__info .section__inner>dl,
  .groups__add__update .group__info .section__inner>ul>li>dl {
    flex-direction: column;
    align-items: flex-start;
  }

  .groups__add__update .group__info .section__inner>dl dt,
  .groups__add__update .group__info .section__inner>ul>li>dl dt {
    margin-bottom: 12px;
  }

  .groups__add__update .ota__info .list__wrap>li>dl {
    flex-direction: column;
    align-items: flex-start;
  }

  .groups__add__update .ota__info .list__wrap>li>dl>dt {
    margin-bottom: 12px;
  }

  .groups__add__update .ota__info .list__wrap>li:nth-of-type(2) li:first-of-type {
    width: 100%;
    margin-right: 0px;
    margin-bottom: 16px;
  }

  .groups__add__update .ota__info .list__wrap>li>dl>dd>ul {
    flex-wrap: wrap;
  }

  .content.groups__add__update .sub__nav {
    flex-direction: column;
    align-items: flex-start;
  }

  .content.groups__add__update .sub__nav>.left {
    margin-bottom: 18px;
  }
}